.tasks {
  margin: 0;
  padding: 0;
  list-style: none;
}

.tasks__item {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-gap: 10px;
  border-bottom: 1px solid #ddd;
  align-items: center;
  padding: 10px;
}

.tasks__done {
  text-decoration: line-through;
}

.tasks__button {
  height: 30px;
  width: 30px;
  color: white;
  border: none;
  padding: 0;
  transition: background 0.3s;
}

.tasks__button--toggleDone {
  background-color: hsl(120, 100%, 25%);
}

.tasks__button--toggleDone:hover {
  background-color: hsl(120, 100%, 30%);
  cursor: pointer;
}

.tasks__button--toggleDone:active {
  background-color: hsl(120, 100%, 35%);
}

.tasks__button--remove {
  background-color: hsl(348, 83%, 47%);
}

.tasks__button--remove:hover {
  background-color: hsl(348, 83%, 52%);
  cursor: pointer;
}

.tasks__button--remove:active {
  background-color: hsl(348, 83%, 57%);
}

.tasks__done--hidden {
  display: none;
}